@mixin button {
    background-color: $primary;
    color: white;
    border-radius: 7px;
    border-color: transparent;
    text-align: center;
}

@mixin slider-text {
    .slider-image {
        width: 100%;
        

    }

    .slider-text {
        text-align: right;
        color: white;

        .text {
            font-size: 54px;
            font-weight: bold;
            position: absolute;
            text-align: center;
            width: 500px;
            @content;
            @include media-breakpoint-down(lg) {
                @content;
            }
            @include media-breakpoint-down(sm) {
                right: 0;
                font-size: 30px;
                width: 240px;


                @content;
            }

        }
    }
}

@mixin section-title {
    .section-title {
        color: $main;
        margin-bottom: 28px;
        font-weight: bold;

        @include media-breakpoint-down(sm) {
            text-align: center;
        }
        @content;

        &::after {
            content: "";
            display: block;
            width: 22%; // 263px
            height: 3px;
            background-color: $primary;
            margin-top: 7px;
            @include media-breakpoint-down(sm) {
                margin: auto;
            }
            @include media-breakpoint-down(lg) {
                width: 36%;
            }

            @include media-breakpoint-down(md) {
                width: 45%;
            }

            @content;
        }

        margin-top: 4px;
    }

}

@mixin section-title-center {
    @content;
    @include section-title {

        text-align: center;
        @content;
        &::after {
            margin: auto;
            @content;
        }
    }
}

@mixin section-title-center-small {
    .main-title {

        color: $main;
        margin-bottom: 17px;

        font: {
            size: 20px;
            weight: bold;
        }



        &::after {
            content: "";
            display: block;
            margin: auto;
            width: 70%; //  96.5px
            height: 2px;

            background-color: $primary;
        }
    }

}

@mixin section-description {

    .section-description {
        font-size: 25px;
        color: $paragraph;
        text-align: justify;
        @content ;
    }
}

