@mixin introduction-text {
    .intro-text {
        color: $paragraph;
        font-size: 25px;
        text-align: justify;
        line-height: 1.3;

        @content;

        @include media-breakpoint-down(sm) {
            font-size: 20px;
        }
    }
}

@mixin minimumTitle {
    .title {
        text-align: center;
        color: $main;
        font-size: 25px;
        font-weight: bold;
        margin-top: 60px;

    }
}

@mixin siteLink {
    .siteLink {
        text-decoration: none;
        color: $paragraph;

        &:hover {
            color: $main;
        }
    }
}

// grid system title
.grid-title{
    border-right: 2px solid $primary;
    margin-bottom: 24px ;
    .text{
        font-size: 36px;
        font-weight: 500;
        color: $main;
        text-align: right;
        
    }

}
// grid system
.grid-box{
    text-align: center;
    padding: 25px 30px;
    box-shadow: 0px 5px 11px 0px #0929501a;
    border-radius: 10px;
    margin: 0px  0px 45px 0px ;
     
    .grid-first-description{
        font-size: 20px ;
        font-weight: 500;
        color: $main;
        margin-top: 8px;
    }
    .grid-sec-description{
        font-size: 25px;
        margin: 16px 0 0 0 ;
        font-weight: bold;
        color: $main;
    }
}


//image slider style
.slider-item{
    position: relative;
    .slider-image{
        width: 100%;
        z-index: -1;
    }
    .overlay{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%; 
        background-image: linear-gradient(to right,#fff,$main );
        opacity: .4;
        z-index: 0;
        border: none;
    }
}

//text slider style

.slider-text{
    position: absolute;
    top: 20%;
    color: #fff;
    display: block;
    @include media-breakpoint-down(xl) {
        top: 7%;
    }
    @include media-breakpoint-down(sm) {
        text-align: center;
        top: 5%;
    }
            .slider-title{
                font-weight: bold;
                width: 400px;
                line-height: 1.5;
                margin-bottom: 25px;
                @media  (max-width : 375px) {
                    font-size:15px !important;
                    
                }
                @include media-breakpoint-down(xl) {
                    margin-bottom: 10px;
                    font-size: 30px;
                }
                @include media-breakpoint-down(md) {
                    font-size: 23px;
                }
                @include media-breakpoint-down(sm) {
                    font-size: 19px;
                    width: auto;
                }
                @media  (max-width : 375px) {
                    margin-bottom: 5px;
                }

            }
            .slider-descreption{
                font-size: 20px;
                margin-bottom: 30px;
                @include media-breakpoint-down(xl) {
                    margin-bottom: 19px;
                }
                @include media-breakpoint-down(sm) {
                    margin-bottom: 10px;
                    font-size: 12px;
                }
                @media  (max-width : 375px) {
                    margin-bottom: 5px;
                }
                .first-paragraph{
                    display: block;
                    margin-bottom: 25px;
                    @include media-breakpoint-down(xl) {
                        margin-bottom: 10px;
                    }
                    @include media-breakpoint-down(sm) {
                        margin-bottom: 0px;
                    }
                    @media  (max-width : 375px) {
                        font-size: 10px !important;
                        
                    }
                }
            }
            
        }


// section main title
.text-box{
    margin: 50px auto;
    @include section-title-center;
    
    @include section-description{
        font-weight: 500;
        text-align: center;
    }
    .main-description{
        margin-bottom: 35px;
    }

}