.btn.btn-primary {
  background-color: #dc6e65;
  border: none;
  font-size: 25px;
  border-radius: 0;
  margin-left: 8px;
  font-weight: 700;
  &.shadow {
    box-shadow: 0em 0.15em 0.25em 0em rgba(0, 0, 0, 0.25);
  }
  &.dark {
    background-color: $main !important;
  }
}

.main-footer {
  background-color: $main;
  padding: 70px 20px;
  .nav {
    direction: ltr;
  }
  .text-muted {
    font-size: 20px;
    color: #fff !important;
    text-decoration: none;
  }
  .text-muted-first {
    margin-right: 20px;
    position: relative;
    &::after {
      content: "";
      height: 24px;
      width: 1px;
      background-color: #ffffff;
      display: block;
      margin-left: 20px;
      position: absolute;
      top: 4px;
      right: -10px;
    }
  }
}

.mainHeader {
  background-color: #fff;
  padding: 10px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .navbar {
    font-size: 21px;

    .navbarList {
      .headerLink {
        color: white;
        font-size: 21px;
        margin-left: 5px;

        &:hover {
          color: #fff;
        }

        @include media-breakpoint-between(lg, xl) {
          font-size: 14px;
        }
        @include media-breakpoint-between(xl, xxl) {
          font-size: 18px;
        }
      }
    }

    .headerImage {
      height: 45px; //252px
      @include media-breakpoint-down(sm) {
        height: 30px;
      }
      @include media-breakpoint-between(lg, xl) {
        height: 35px;
      }
      @include media-breakpoint-between(xl, xxl) {
        height: 40px;
      }
    }
  }
  .burger-menu:focus {
    border: transparent;
  }
  .burger-menu {
    background-color: transparent;
    border: none;
  }
  .lang-box {
    display: flex;
    font-size: 20px;
    font-weight: 500;
    p {
      margin: 0;
      color: $main;
    }
    .checkbox {
      width: 70px;
      height: 37px;
      border: 6px solid $main;
      border-radius: 36px;
      position: relative;
      cursor: pointer;
      transition: ease all 0.5s;
      .circle {
        width: 26px;
        height: 25px;
        background-color: $primary;
        border-radius: 50%;
        position: absolute;
        right: 0;
      }
    }
  }
  .offcanvas {
    background-color: $primary;
    color: #fff;
    .offcanvas-title {
      font-size: 25px;
    }
    .list {
      text-decoration: none;
      list-style: none;
      text-align: center;
      .list-item {
        font-size: 20px;
        margin-bottom: 20px;
        &::after {
          content: "";
          height: 1px;
          width: 40px;
          background-color: #fff;
          display: block;
          margin: auto;
          margin-top: 5px;
        }
        .link {
          color: #fff;
          text-decoration: none;
        }
      }
    }
  }
}
@media (max-width: 767.98px) {
  .logo {
    width: 100%;
    height: 100%;
  }
}

