.form-section{
    margin: 80px auto;
    @include section-title-center{
        &::after{
            width: 60% !important;
        }
    }
    .flex-form{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        .form-list{
            padding: 100px;
            @include media-breakpoint-down(sm) {
                padding: 15px !important;
            }
            @include media-breakpoint-down(lg) {
                padding: 60px;
                width: 80%;
                margin-bottom: 50px;
            }
            @include media-breakpoint-down(xl) {
                padding: 50px;
            }
            background-color: $section;
            width: 50%;
            .submit-form{
                text-align: center;
            }
            .input-box{
                margin: 40px auto;
                display: flex;
                border-bottom: 2px solid $label-form;
                
                .details{
                    font-size: 20px;
                    color: $label-form;
                    margin-bottom: 3px;
                    flex: none;
                }
                .text{
                    border: none;
                    outline: none;
                    background-color: $section;
                    width: 100%;
                }
            }
        }
        .more-information{
            padding: 38px;
            background-color: $section;
            width: 40%;
            height: 20%;
            @include media-breakpoint-down(sm) {
                padding: 5px;
            }
            @include media-breakpoint-down(xl) { 
                width: 45%;
             }
             @include media-breakpoint-down(lg) {
               
                width: 70%;
            }
            @include section-title{
                text-align: right;
                    &::after{
                        width: 60% !important;
                        margin: 0px ;
                    }
            }
            .contact-icon{
                margin: 15px auto;
                @include media-breakpoint-down(lg) {
                    text-align: center;
                }
                .contact-details{
                    font-size: 25px;
                    font-weight: bold;
                    color: $main;
                    margin-right: 5px;
                    @include media-breakpoint-down(lg) {
                        text-align: center;
                    }
                }
            }
            .contact-text{
                margin: 20px auto;
                color: $main;
                @include media-breakpoint-down(sm) {
                    text-align: center;
                }
                .contact-text-details{
                    font-size: 22px;
                    font-weight: bold;
                    margin: 0px;
                    .contact-details-word{
                        font-size: 22px;
                        font-weight: 500;
                    }

                }
                .contact-text-details:last-child{
                    font-size: 15px;
                    color: $main;
                }
            }
        }
    }
            
}


