  .tech-content {
    background-color: #416f8b;
    display: flex;
    flex-direction: column;
    align-items: center;
    //     height: 580px;
    padding: 25px;
    height: 646px;
  }
  .title {
    color: #dc6e65;
    font-size: 50px;
    font-weight: bold;
    width: 48%;
    margin-bottom: 43px;
    animation: fadeDown 3s ease-in-out forwards;
  }
  .description {
    width: 55%;
    text-align: center;
    font-size: 28px;
    color: #fff;
    margin-bottom: 25px;
    animation: fadeDown 3s ease-in-out forwards;
  }
  .tech-details {
    animation: fadeDown 3s ease-in-out forwards;
    display: flex;
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
    position: relative;
    top: -86px;
    padding: 0px 10%;
    .tech-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      .tech-title {
        color: #000;
        font-size: 24px;
        font-weight: 700;
        width: 77%;
      }
      a {
        color: #3f3434;
        font-size: 22px;
      }
    }
  }
}
@media (max-width: 767.98px) {
  .tech .title {
    width: 100%;
    font-size: 30px;
  }
  .tech .description {
    width: 100%;
    font-size: 18px;
  }
}

