body {
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translatex(-50px); // Slight slide effect (optional)
  }
  to {
    opacity: 1;
    transform: translatex(0);
  }
}
@keyframes fadeDown {
  from {
    opacity: 0;
    transform: translatey(-50px); // Slight slide effect (optional)
  }
  to {
    opacity: 1;
    transform: translatey(0);
  }
}

